                        <img class='videoSelectIcon' src="../assets/images/videoNoSelectIcon.png" alt="" v-else>
                        <div class="leftMain">
                            <p class="selectName">Basic</p>
                            <p class="selectScript">Publish to single platform </p>
                            <p class="earnText">Expected to earn: {{moneySymbol+'99,999.99'}}</p>
                        </div>
                    </div>
                    <div class="itemRight">
                        <p class="feeText">Rental fee</p>
                        <p class="feeNumber">{{moneySymbol+'--'}}</p>
                    </div>
                </div>
                <p class="selectAlertText">According to fair trade rules, advanced publish will take up more resources and will also bring in more advertising revenue, so additional rent will be paid.</p>
                <div class="submitBtn_91 GradientAllowBtn">Rent Now</div>
