        </van-popup>
        <van-popup v-model="showUpgreadAlert" class='upgreadAlertBox flexCenter' style="">
            <img class='modelCloseIcon' src="../assets/images/modelCloseIcon.png" alt="" @click="showUpgreadAlert = false">
            <img class='upgreadIcon' src="../assets/images/upgreadIcon.png" alt="">
