                <img class='smallVipIcon' src="../assets/images/smallV4Icon.png" alt="" v-else-if="requireLevel == 4">
                <img class='smallVipIcon' src="../assets/images/smallV5Icon.png" alt="" v-else-if="requireLevel == 5">
                <span class='blueText'> {{'v'+requireLevel}}</span>
            </div>
            <div class="currentText flexStart">
                <span class='redText'>Current level: </span>
                <img class='smallVipIcon' src="../assets/images/smallV0Icon.png" alt="" v-if="myLevel == 0">
                <img class='smallVipIcon' src="../assets/images/smallV1Icon.png" alt="" v-else-if="myLevel == 1">
                <img class='smallVipIcon' src="../assets/images/smallV2Icon.png" alt="" v-else-if="myLevel == 2">
                <img class='smallVipIcon' src="../assets/images/smallV3Icon.png" alt="" v-else-if="myLevel == 3">
                <img class='smallVipIcon' src="../assets/images/smallV4Icon.png" alt="" v-else-if="myLevel == 4">
                <img class='smallVipIcon' src="../assets/images/smallV5Icon.png" alt="" v-else-if="myLevel == 5">
                <span class='redText'> {{'v'+myLevel}}</span>
            </div>
            <div class="submitBtn_91 GradientAllowBtn" style="margin-top: .56rem;width: 100%;" @click="goPage('/VIP')">Upgrade Now</div>
        </van-popup>
        <van-popup v-model="showUpgreadAlert2" class='upgreadAlertBox flexCenter' style="">
            <img class='modelCloseIcon' src="../assets/images/modelCloseIcon.png" alt="" @click="showUpgreadAlert2 = false">
            <img class='upgreadIcon' src="../assets/images/upgreadIcon.png" alt="">
            <p class="upgreadText" style="margin-bottom: 0;">Upgrade your membership level<br>to get full rewards</p>
            <div class="submitBtn_91 GradientAllowBtn" style="margin-top: .72rem;width: 100%;" @click="goPage('/VIP')">Upgrade Now</div>
            <p class="laterText" @click="showUpgreadAlert2 = false">Remind me later</p>
        </van-popup>
        <van-popup v-model="showBalanceAlert" class='upgreadAlertBox flexCenter' style="">
            <img class='modelCloseIcon' src="../assets/images/modelCloseIcon.png" alt="" @click="showBalanceAlert = false">
            <img class='balanceNotEnoughIcon' src="../assets/images/balanceNotEnough.png" alt="">
            <p class="balanceNotText">Account balance is<br>not enough to continue</p>
            <p class="balanceNotText1">Need to pay: {{moneySymbol + ' '+detailData.rentalFee}}</p>
            <p class="balanceNotText1" style="color: #FF305F;margin-top: .06rem;">Current balance: {{moneySymbol + ' '+balance}}</p>
            <div class="submitBtn_91 blueAllowBtn" style="margin-top: .56rem;width: 100%;" @click="goPage('/Recharge')">Recharge Now</div>
        </van-popup>
		<DescriptionPage :errorPageActive='errorPageActive' :titleText='errorPageTitle' :scriptText='errorPageScript' :btnText='errorPageBtnText'></DescriptionPage>
	</div>
</template>
<script>
	import DescriptionPage from '../components/DescriptionPage'
	import Clipboard from 'clipboard'
	import https from '../api/https'
	export default {
		components: {
			DescriptionPage,
		},
		name: 'VideoDetails',
		data: function(){
			return {
				errorPageActive: false,
				errorPageTitle: '',
				errorPageScript: '',
				errorPageBtnText: 'Back',

				VHinvite_code: '',
				bgWrapHeight: 200,
                openStroe: true,
                moneySymbol: https.moneySymbol,
				detailData: null,
                charts: {
                    columns: [],
                    values: []
                },
				rentSuccess: false,
				cancelAlertBox: false,
				showShareBox: false,
				sysType: 'Android',
				FacebookUrl: '',
				WhatsAppUrl: '',
				SMSUrl: '',
				EmailUrl: '',
				
				selectLevel: '1',

				showUpgreadAlert: false,
                showSelectModel: false,
                myLevel: '',
                showUpgreadAlert2: false,
                showBalanceAlert: false,
				remainingPost: '',
                isBlack: '',
                balance: '',
			}
		},
		created(){
			if(this.$route.params.VHinvite_code && this.$route.params.VHinvite_code != 'undefined') {
				localStorage.setItem('VHinvite_code',this.$route.params.VHinvite_code)
				this.VHinvite_code = this.$route.params.VHinvite_code
			}else if(localStorage.getItem('VHinvite_code') && localStorage.getItem('VHinvite_code') != 'undefined') {
				this.VHinvite_code = localStorage.getItem('VHinvite_code')
			}else {
                this.VHinvite_code = ''
				localStorage.setItem('VHinvite_code','')
            }
			
			let u = navigator.userAgent
			let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
			let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
			if (isAndroid) {
				this.sysType = 'Android'
			}else if (isIOS) {
				this.sysType = 'IOS'
			}

			
			if(localStorage.getItem('videoData')) {
				this.detailData = JSON.parse(localStorage.getItem('videoData'))
				this.about = this.detailData.about
				this.myLevel = this.detailData.myLevel
				this.requireLevel = this.detailData.requireLevel
				this.remainingPost = this.detailData.remainingPost
				this.isBlack = this.detailData.isBlack
				this.balance = this.detailData.balance

				if(this.detailData.share && this.detailData.share.url) {
					let str1 = 'This is a great movie clip.'
					let resultUrl = ''
					
					if(this.sysType == 'Android') {
						resultUrl = 'sms: '+'?body='+str1+this.detailData.share.url
					}else if (this.sysType == 'IOS') {
						resultUrl = 'sms: '+'&body='+str1+this.detailData.share.url
					}else {
						resultUrl = 'sms: '+'?body='+str1+this.detailData.share.url
					}
					
					this.FacebookUrl = 'fb://friends'
					// this.WhatsAppUrl = "https://wa.me/?text=" + str1 +this.detailData.share.url
					this.WhatsAppUrl = "whatsapp://send?text=" + str1 +this.detailData.share.url
					
					this.SMSUrl = resultUrl
					this.EmailUrl = 'mailto:&body='+str1+this.detailData.share.url
				}
			}else {
				this.errorPageTitle = 'Invalid argument'
				this.errorPageScript = 'Please check the parameters'
				this.errorPageBtnText = 'Back'
				this.errorPageActive = true
			}
		},
		mounted: function () {
			let viwePortHeight = document.documentElement.clientHeight || document.body.clientHeight
			let navBarHeight = this.$refs.navBarHeight.offsetHeight
			this.bgWrapHeight = viwePortHeight - navBarHeight
			if(this.detailData && this.detailData.recently.length > 0) {
				for (let index = 0; index < this.detailData.recently.length; index++) {
					this.charts.columns.push(this.detailData.recently[index].date)
					this.charts.values.push(this.detailData.recently[index].earn)
				}
				setTimeout(() => {
					this.drawChart()
				}, 200);
			}
		},
		methods: {
            drawChart() {
                let myChart = this.$echarts.init(document.getElementById("main"));
                console.log(myChart)
				let option = {
					
					textStyle: {
						color: '#BAC3CC',
					},
					grid: {
						show: true,
						top: '10%',
						// bottom: '10%',
						// right: '4%',
						left: '2.5%',
						width: '90%',
						height: '90%',
						containLabel: true,
						borderColor: '#fff',
						// shadowBlur: 10,
						// shadowColor: '#FF9234'
					},
					xAxis: {
						data: this.charts.columns,
						type:'category',
						boundaryGap: true,
						axisLine: {
                            type: 'dashed',
                            show: false,
							lineStyle : {
								color: '#BAC3CC'
							}
						},
						axisTick: {
							// alignWithLabel: true,
                            show: false,
							interval: 0,
							lineStyle: {
								color: '#BAC3CC'
							}
						},
						axisLabel: {
							// interval: 0,
						},
						splitLine: {
							show: false,
							interval: 0,
							lineStyle: {
								color: '#BAC3CC',
                                width: 0,
							}
						},
                        minorSplitLine: {
                            show: true,
                            color: 'green'
                        }
                       
                        
						
					},
					yAxis:	 {
						type: 'value',
						position: 'left',
						axisLine: {
							show: false,
							lineStyle : {
								color: '#BAC3CC'
							}
						},
						// min: 'dataMin',
						// max: 'dataMax',
						splitNumber: '4',
						// interval: '2',
						axisLabel: {
							show: true,
							interval: 2,
							// showMinLabel: false,
							// showMaxLabel:false,
						},
						axisTick: {
							show: false,
							interval: 2,
							lineStyle: {
								color: '#BAC3CC'
							}
						},
						splitLine: {
							show: true,
							interval: 2,
							lineStyle: {
                                type: 'dashed',
								color: '#BAC3CC',
                                join:'bevel'
							}
						},
						// axisPointer: {
						// 	show: true,
						// 	type: 'shadow'
						// }
						// data: this.productList.charts.values,
						// type:'category',
						// boundaryGap: true,
					},
					series: [
						{
							// name: "num",
							color: '#FF9234',
							type: "line",
							data: this.charts.values
						}
					]
				}
				myChart.setOption(option);
			},
			videoRental: function(id){
				this.showLoading = true
				setTimeout(() => {
                    if(this.showLoading) {
                        this.$toast.loading({
                            message: 'Loading...',
                            forbidClick: true,
                            loadingType: 'spinner',
                            icon: require('../assets/images/loadingIcon.png'),
                            duration: 0,
                            className:'toastLoading'
                        });
                    }
                }, 500);
                let params = {
                    type: 'rent',
                    product: id,
                }
				https.fetchPost('/api/pay/cashier/create',params).then((data) => {
                    this.showLoading = false
					this.$toast.clear()
					let res = data.data
					if(res.code == 0) {
                        this.rentSuccess = true
					}else if(res.code == -2 || res.code == -3) {
						this.goPage("/Login")
					}else {
						this.$toast({
							message: res.msg,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
					}
				}).catch(err=>{
					console.log(err)
					if(this.NetWorkRetryTimes && this.NetWorkRetryTimes >= 1) {
						this.showLoading = false
						this.$toast({
							message: https.NetworkErrorText,
							forbidClick: true,
							icon: require('../assets/images/errorIcon.png'),
							duration: 2000,
							className: 'resultToast'
						});
						this.NetWorkRetryTimes = 0
					}else {
						this.NetWorkRetryTimes = 0
						this.NetWorkRetryTimes++
						this.getData()
					}
				})
			},
			goRental(item){
                if(this.isBlack == true) {
                    this.$toast.loading({
                        message: 'Video has been\nrented out',
                        forbidClick: true,
                        duration: 2000,
                        className:'smallLoading'
                    });
                }else if(this.remainingPost <= 0) {
                    this.showUpgreadAlert2 = true
                }else if(this.myLevel/1 < this.requireLevel/1) {
                    this.showUpgreadAlert = true
                }else if(this.balance/1 < item.rentalFee) {
                    this.showBalanceAlert = true
                }else if(item.rentalFeeAdv) {
                    this.showSelectModel = true
                }else {
                    this.videoRental(item.id)
                }
            },
			copyText(type){
				let that = this
				// this.toastText.close();
                var clipboard = new Clipboard('.copyBtn');
                clipboard.on('success', function() {
					that.$toast({
						message: type+' Copied',
						forbidClick: true,
						duration: 2000,
						className: 'smallToast'
					});
                });

                clipboard.on('error', function() {
					that.$toast({
						message: 'Copy failed',
						forbidClick: true,
						duration: 2000,
						className: 'smallToast'
					});
                });
			},
			shareCopy() {
				// let that = this
				// this.toastText.close();
                var clipboard = new Clipboard('.copyBtn');
                clipboard.on('success', function() {
					// that.$toast({
					// 	message: type+' Copied',
					// 	forbidClick: true,
					// 	duration: 2000,
					// 	className: 'smallToast'
					// });
                });
                clipboard.on('error', function() {
					// that.$toast({
					// 	message: 'Copy failed',
					// 	forbidClick: true,
					// 	duration: 2000,
					// 	className: 'smallToast'
					// });
                });
			},
			goPage(page){
				if(this.VHinvite_code) {
					page = page +'/'+ this.VHinvite_code
				}
				this.$router.push(page)
			},
			goBack(){
				this.$router.go(-1)
			},
			openShareWindow(type,newUrl){
				let str1 = 'This is a great movie clip.'
				let resultUrl = ''
				if(type == 'WhatsApp') {
					resultUrl =	"https://wa.me/?text=" + str1 +newUrl
				}else if(type == 'SMS') {
					resultUrl =	'smsto:?body='+str1+newUrl
					if(this.sysType == 'Android') {
						resultUrl = 'sms: '+'?body='+str1+newUrl
					}else if (this.sysType == 'IOS') {
						resultUrl = 'sms: '+'&body='+str1+newUrl
					}else {
						resultUrl = 'sms: '+'?body='+str1+newUrl
					}
				}else if(type == 'Email') {
					resultUrl =	'mailto:&body='+str1+newUrl
				}else if(type == 'Facebook') {
						resultUrl = "http://www.facebook.com/sharer.php?u=" + str1+newUrl
				}
				window.open(resultUrl);
			},
		},
		destroyed(){
			// localStorage.setItem('videoData', null)
		}
	}
</script>
